const ajax = new XMLHttpRequest();  
const content = document.createElement('div')
const root = document.getElementById('root')
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json'
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json'

function getData (url){
    ajax.open('GET', url, false)
    ajax.send(); 

    return JSON.parse(ajax.response);
}

// ÏΩîÎìú Ïû¨ÌôúÏö©ÏùÑ ÏúÑÌï¥ Ìï®ÏàòÎ°ú Î¶¨Ìå©ÌÜ†ÎßÅÌïúÎã§.
// ÎùºÏö∞ÌÑ∞ÏóêÏÑú Í∏Ä Î™©Î°ùÌôîÎ©¥ÏùÑ Ìò∏Ï∂ú
function newsFeed(){
    const newsFeed = getData(NEWS_URL);
    const newsList = [];
    newsList.push('<ul>')
    for(let i=0; i<10; i++){
        console.log(newsFeed.length)
        newsList.push(`
            <li>
                <a href="#${newsFeed[i].id}">${newsFeed[i].title}(${newsFeed[i].comments_count})</a>
            </li>
        `)
    }
    newsList.push('</ul>')
    root.innerHTML = newsList.join('')
}

//ÎùºÏö∞ÌÑ∞ÏóêÏÑú Í∏Ä ÎÇ¥Ïö©ÌôîÎ©¥ Ìò∏Ï∂úÏùÑ ÏúÑÌï¥ÏÑú, Ìï®Ïàò Ïù¥Î¶ÑÏù¥ ÌïÑÏöî. 
function newsDetail(){
    
    const id = location.hash.substr(1)
    const newsContent = getData(CONTENT_URL.replace('@id', id));

    root.innerHTML = `
        <span><a href="#">Î™©Î°ùÏúºÎ°ú</a></span>
        <h1>${newsContent.title}</h1>
    `
}

//newsFeedÎ•º Ï≤òÏùåÏóê Ìò∏Ï∂úÌïòÎäî ÎùºÏö∞ÌÑ∞ Ìï®ÏàòÍ∞Ä ÌïÑÏöîÌï® 
function router(){
    const routePath = location.hash
    //location.hash Ïóê #Îßå ÏûàÏùÑ Í≤ΩÏö∞, Îπà Í∞íÏùÑ Î∞òÌôò
    if(routePath === ''){
        newsFeed()
    }else{
        newsDetail()
    }
}
window.addEventListener('hashchange', router)

// window onloadÏãú, routerÌò∏Ï∂ú
router()